:root{
    --background-color: #181818;
    --color: #EEEEEE;
    --color-hover: #cecece;
    --additional-color: #24B47E;
    --additional-color-hover: #1d835d;
    --border-radius: 4px;
}
body{
  margin: 0;
  padding: 0;
  color: var(--color);
  background-color: var(--background-color);
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 400;
}

.styles_header__3KTMF {
    margin: 0;
    padding: 0;
    padding: 0 160px;
    display: flex;
    justify-content: space-between;
  }
.routeTransition_enter__oFGRl {
  opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  
  .routeTransition_enterActive__3xiJN {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    transition: opacity 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
    transition: transform 200ms ease-in-out, opacity 200ms ease-in-out;
    transition: transform 200ms ease-in-out, opacity 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
  }
  
  .routeTransition_exit__1kVIL {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  
  .routeTransition_exitActive__2GyPl {
    opacity: 0;
    -webkit-transform: translateY(200%);
            transform: translateY(200%);
    transition: opacity 200ms ease-in-out , -webkit-transform 200ms ease-in-out;
    transition: transform 200ms ease-in-out , opacity 200ms ease-in-out;
    transition: transform 200ms ease-in-out , opacity 200ms ease-in-out , -webkit-transform 200ms ease-in-out;
  }
p{
  margin: 0;
  padding: 0;
  margin-left: 10px;
  display: block;
  padding-top: 23px;
  padding-bottom: 23px;
}

.app_nav__6eJmg,
.app_authnav__3DB9d,
.app_userMenu__28B01 {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
}

.app_nav__6eJmg > li,
.app_authnav__3DB9d > li,
.app_userMenu__28B01 > li {
  margin: 0;
}

.app_logoutButton__vjjQH{
  font-size: 16px;
  font-weight: 400;
  width: 120px;
  height: 65px;
  display: inline-block;
  color: var(--color);
  background-color: var(--background-color);
  border: none;
  transition: color 250ms ease;
}

.app_logoutButton__vjjQH:hover{
  color: var(--color-hover);
}

.app_userItem__1_BF-{
  margin: 0;
  padding: 0;
  width: 43px;
  height: 43px;
  padding: 11px;
}

.app_avatar__PXFTF{
  height: 100%;
  width: 100%;
  display: block;
  object-fit: cover;
  object-position: center;
  padding: 0;
  margin: 0;
  border-radius: 50%;
}

.app_link__1Exax {
  display: block;
  text-decoration: none;
  color: var(--color);
  padding: 21px 20px;
  transition: color 250ms ease;
}

.app_link__1Exax:hover {
  color: var(--color-hover);
}

.app_activeLink__2oMla {
  color: var(--additional-color);
}

.app_logo__23x5P{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 130px;
  padding-right: 60px;
}
