.enter {
  opacity: 0;
    transform: translateY(-100%);
  }
  
  .enterActive {
    opacity: 1;
    transform: translateY(0);
    transition: transform 200ms ease-in-out, opacity 200ms ease-in-out;
  }
  
  .exit {
    opacity: 1;
    transform: translateY(0);
  }
  
  .exitActive {
    opacity: 0;
    transform: translateY(200%);
    transition: transform 200ms ease-in-out , opacity 200ms ease-in-out;
  }